<script>
  import mixin from '../util/mixin'
  import { clearEmptyButBoolean } from '../../../utils/snippet.js'
  export default {
    name: 'amap-polyline',
    mixins: [mixin],
    props: {
      path: {
        type: Array,
        required: true
      },
      editable: Boolean,
      extData: null,
      zIndex: Number,
      isOutline: Boolean,
      borderWeight: Number,
      outlineColor: String,
      lineJoin: String,
      showDir: Boolean,
      geodesic: Boolean,
      bubble: Boolean,
      strokeColor: String,
      strokeOpacity: Number,
      strokeWeight: Number,
      strokeStyle: String,
      strokeDasharray: Array
    },
    watch: {
      path (v) {
        this.overlay.setPath(v)
      },
      extData (v) {
        this.overlay.setExtData(v)
      }
    },
    methods: {
      load () {
        const AMap = global.AMap
        return new Promise(resolve => {
          this.overlay = new AMap.Polyline(clearEmptyButBoolean({
            path: this.path,
            extData: this.extData
          }))
          this.setOptions()
          this.overlay.setMap(this.amap)
          if (this.editable) {
            global.AMap.plugin(['AMap.PolyEditor'], () => {
              this.editor = new global.AMap.PolyEditor(this.amap, this.overlay)
              resolve(this.overlay)
            })
          } else {
            resolve(this.overlay)
          }
        })
      },
      setOptions () {
        this.overlay.setOptions(clearEmptyButBoolean({
          zIndex: this.zIndex,
          isOutline: this.isOutline,
          borderWeight: this.borderWeight,
          outlineColor: this.outlineColor,
          lineJoin: this.lineJoin,
          showDir: this.showDir,
          geodesic: this.geodesic,
          bubble: this.bubble,
          strokeColor: this.strokeColor,
          strokeOpacity: this.strokeOpacity,
          strokeWeight: this.strokeWeight,
          strokeStyle: this.strokeStyle,
          strokeDasharray: this.strokeDasharray
        }))
      },
      contains (point) {
        return this.overlay.contains(point)
      }
    }
  }
</script>
