<script>
  import mixin from '../util/mixin'
  import { PATH_SIMPLIFIER } from '../events'
  export default {
    name: 'amap-path-simplifier',
    props: {
      zIndex: {
        type: Number,
        default: 100
      },
      renderOptions: Object,
      paths: Array,
      selectedPathIndex: Number,
      getHoverTitle: Function
    },
    mixins: [mixin],
    watch: {
      paths: 'setPath',
      selectedPathIndex (v) {
        if (!this.pathSimplifier) {
          return
        }
        this.pathSimplifier.setSelectedPathIndex(v)
      }
    },
    data () {
      this.navigators = []
      return {}
    },
    methods: {
      load () {
        return new Promise(resolve => {
          const { AMapUI } = this
          AMapUI.load(['ui/misc/PathSimplifier'], (PathSimplifier) => {
            this.PathSimplifier = PathSimplifier
            this.init()
            resolve(this.pathSimplifier)
          })
        })
      },
      init () {
        const { PathSimplifier } = this
        this.pathSimplifier = new PathSimplifier({
          zIndex: this.zIndex,
          map: this.amap,
          renderOptions: this.renderOptions,
          getHoverTitle: this.getHoverTitle,
          getPath: d => d.path
        })
        this.bindEvent(this.pathSimplifier, PATH_SIMPLIFIER)
      },
      setPath () {
        if (!this.pathSimplifier) {
          return
        }
        const { PathSimplifier } = this
        this.navigators.forEach(d => {
          d.destroy()
        })
        this.navigators = []
        let result = []
        const navigators = []
        if (this.pathSimplifier && this.paths.length) {
          result = this.paths.map((d, i) => {
            let path
            if (Array.isArray(d.path)) {
              path = d.path
            } else {
              const { start, end, interpolate = 0 } = d.path
              path = PathSimplifier.getGeodesicPath(start, end, interpolate)
            }
            if (d.navigator) {
              navigators.push({
                index: i,
                navigator: d.navigator
              })
            }
            return {
              name: d.name || '',
              path
            }
          })
        }
        this.pathSimplifier.setData(result)
        this.navigators = navigators.map(d => {
          const nav = this.pathSimplifier.createPathNavigator(d.index, d.navigator)
          nav.start()
          return nav
        })
      }
    }
  }
</script>
