<template>
  <div class="amap-container" :style="{ height: height }">
    <slot></slot>
  </div>
</template>
<script>
  import queuer from '@/utils/queuer.js'
  import { clearEmptyButBoolean } from '@/utils/snippet.js'

  export default {
    name: 'Amap',
    props: {
      ak: {
        type: String,
        default: '4889a2d59bf4153fb3416c60938ae01a'
      },
      height: {
        type: String,
        default: '300px'
      },
      plugins: {
        type: Array,
        default () {
          return ['AMap.ToolBar', 'AMap.Scale']
        }
      },
      enableLoca: Boolean,
      labelzIndex: Number,
      center: {
        type: [Array, String],
        default: '北京'
      },
      zoom: {
        type: Number,
        default: 6
      },
      zooms: {
        type: Array,
        default () {
          return [3, 18]
        }
      },
      viewMode: {
        type: String,
        default: '2D'
      },
      mapStyle: {
        type: String,
        default: 'normal'
      },
      crs: String,
      resizeEnable: {
        type: Boolean,
        default: true
      },
      animateEnable: {
        type: Boolean,
        default: true
      },
      isHotspot: Boolean,
      rotateEnable: Boolean,
      showInddorMap: {
        type: Boolean,
        default: true
      },
      expandZoomRange: {
        type: Boolean,
        default: true
      },
      dragEnable: {
        type: Boolean,
        default: true
      },
      zoomEnable: {
        type: Boolean,
        default: true
      },
      doubleClickZoom: {
        type: Boolean,
        default: true
      },
      keyboardEnable: {
        type: Boolean,
        default: true
      },
      jogEnable: {
        type: Boolean,
        default: true
      },
      scrollWheel: {
        type: Boolean,
        default: true
      },
      touchZoom: {
        type: Boolean,
        default: true
      },
      showBuildingBlock: {
        type: Boolean,
        default: true
      },
      pitchEnable: {
        type: Boolean,
        default: true
      },
      buildingAnimation: {
        type: Boolean,
        default: true
      },
      skyColor: String,
      features: Array
    },
    watch: {
      mapStyle () {
        this.amap && this.amap.setMapStyle('amap://styles/' + this.mapStyle)
      },
      zoom (v) {
        this.amap && this.amap.setZoom(v)
      },
      labelzIndex (v) {
        this.amap && this.amap.setlabelzIndex(v)
      },
      center (v, o) {
        console.log(`center change: ${v} ------ ${o}`)
        if (typeof v === 'string') {
          this.amap && this.amap.setCity(v)
          console.log(v)
        } else {
          this.amap && this.amap.setCenter(v)
        }
      }
    },
    data () {
      return {
        queue: queuer()
      }
    },
    activated () {
      this.queue.reset()
      this.reset()
    },
    mounted () {
      this.reset()
    },
    beforeDestroy () {
      if (this.amap) {
        this.amap.destroy()
      }
    },
    methods: {
      init () {
        this.amap = new this.AMap.Map(this.$el, clearEmptyButBoolean({
          zoom: this.zoom,
          zooms: this.zooms,
          mapStyle: 'amap://styles/' + this.mapStyle,
          crs: this.crs,
          animateEnable: this.animateEnable,
          isHotspot: this.isHotspot,
          rotateEnable: this.rotateEnable,
          resizeEnable: this.resizeEnable,
          showIndoorMap: this.showIndoorMap,
          expandZoomRange: this.expandZoomRange,
          dragEnable: this.dragEnable,
          zoomEnable: this.zoomEnable,
          doubleClickZoom: this.doubleClickZoom,
          keyboardEnable: this.keyboardEnable,
          jogEnable: this.jogEnable,
          scrollWheel: this.scrollWheel,
          touchZoom: this.touchZoom,
          labelzIndex: this.labelzIndex,
          features: this.features,
          showBuildingBlock: this.showBuildingBlock,
          viewMode: this.viewMode,
          pitch: this.pitch,
          pitchEnable: this.pitchEnable,
          buildingAnimation: this.buildingAnimation,
          skyColor: this.skyColor
        }))
        if (typeof this.center === 'string') {
          this.amap.setCity(this.center)
        } else {
          this.amap.setCenter(this.center)
        }

        this.$emit('ready', this.amap)
        this.queue.ready()
      },
      async reset () {
        try {
          this.AMap = await this.loadMap()
          this.init()
          if (this.enableLoca) {
            this.loadLoca()
          }
        } catch (e) {
          console.log(e)
        }
      },
      loadMap () {
        if (!global.AMap) {
          global.AMap = {}
          global.AMap._preloader = new Promise((resolve) => {
            global._initAMap = () => {
              const uiScript = document.createElement('script')
              document.body.appendChild(uiScript)
              uiScript.onload = () => {
                document.body.removeChild(uiScript)
                this.AMapUI = global.AMapUI
                resolve(this.loadPlugin())
                document.body.removeChild(script)
                global.AMap._preloader = null
                global._initAMap = null
              }
              uiScript.src = `http://webapi.amap.com/ui/1.0/main.js?v=1.0.11`
            }
            const script = document.createElement('script')
            document.body.appendChild(script)
            script.src = `http://webapi.amap.com/maps?v=1.4.0&key=${this.ak}&callback=_initAMap`
          })
          return global.AMap._preloader
        } else if (!global.AMap._preloader) {
          return this.loadPlugin()
        } else {
          return global.AMap._preloader
        }
      },
      loadLoca () {
        const locaScript = document.createElement('script')
        document.body.appendChild(locaScript)
        locaScript.src = `https://webapi.amap.com/loca?key=${this.ak}`
        locaScript.onload = () => {
          this.$emit('locaReady', this.amap)
        }
      },
      method (name, ...rest) {
        return this.amap[name](...rest)
      },
      // 可加入地图onLoad队列的方法
      onLoad (fn) {
        this.queue.exec(() => {
          fn(this.amap)
        })
      },
      loadPlugin () {
        if (!this.plugins.length) {
          return Promise.resolve(global.AMap)
        }
        return new Promise(resolve => {
          global.AMap.plugin(this.plugins, () => {
            resolve(global.AMap)
          })
        })
      },
      setFitView (overlayList) {
        return this.amap.setFitView(overlayList)
      },
      clearOverlays () {
        return this.amap.clearMap()
      },
      drawCircle (center, radius, options = {}) {
        const overlay = new global.AMap.Circle(center, radius)
        overlay.setOptions(options)
        overlay.setMap(this.amap)
        return overlay
      },
      setCenter (center) {
        this.amap.setCenter(center)
      },
      placeSearch (aoiName) {
        const self = this
        this.AMap.service(['AMap.PlaceSearch'], function () {
          const placeSearch = new self.AMap.PlaceSearch({
            city: '010',
            pageSize: 1,
            pageIndex: 1
          })
          placeSearch.search(aoiName, function (status, result) {
            self.amap.setZoomAndCenter(15, [result.poiList.pois[0].location.lng, result.poiList.pois[0].location.lat])
          })
        })
      }
    }
  }
</script>
