<template>
  <div id="app">
    <amap :height="height" :center="center">
      <amap-polygon ref="tianjin" :path="cityFence.path" :fill-color="'#009b97'" :stroke-weight="0" :stroke-opacity="0"></amap-polygon>
      <template v-for="p in districtFences">
        <amap-polygon
          :key="p.properties.adcode + '_polygon'"
          :path="p.geometry.coordinates[0][0]"
          :fill-color="'transparent'"
          :stroke-weight="2"
          :stroke-style="'dashed'"
          :stroke-color="'#000'"
          :z-index="500"
          ></amap-polygon>
        <amap-marker
          :visible="markerVisible"
          :key="p.properties.adcode + '_marker'"
          :content="getDistrictTitle(p.properties.name)"
          :position="getAreaCenter(p.geometry.coordinates[0][0])"
          :z-index="500"
        ></amap-marker>
      </template>
      <template v-for="p in plates">
        <amap-polygon 
          :key="p.name + '_polygon'"
          :path="p.fence"
          :fill-color="COLOR_MAP[p.color]"
          :stroke-weight="fenceBaseOptions.strokeWeight"
          :stroke-color="fenceBaseOptions.strokeColor"
          :z-index="p.color == 3? 300: p.color ==2 ? 200: 100"></amap-polygon>
        <amap-marker
          :visible="markerVisible"
          :key="p.name + '_marker'"
          :content="p.name"
          :title="p.name"
          :position="getAreaCenter(p.fence)"
        ></amap-marker>
          <!-- :label="{content: p.name}"></amap-marker> -->
      </template>
      </amap>
    <div class="map-control">
      <input type="checkbox" name="show-label" id="show-label" v-model="markerVisible">
      <label for="show-label">显示名字</label>
    </div>
  </div>
</template>

<script>
// #009b97 :stroke-dasharray="[5, 5]"
import '@/components/AMap/'
import city from '@/assets/data/Tianjin.json'
import plates from '@/assets/data/Plates.json'
import districts from '@/assets/data/120000_full.json'

const getAreaCenter = function (lnglatarr) {
    var total = lnglatarr.length;
    var X = 0, Y = 0, Z = 0;
    lnglatarr.forEach(function (lnglat) {
        var lng = lnglat[0] ? lnglat[0] * Math.PI / 180 : lnglat.lng * Math.PI / 180;
        var lat = lnglat[1] ? lnglat[1] * Math.PI / 180 : lnglat.lat * Math.PI / 180;
        var x, y, z;
        x = Math.cos(lat) * Math.cos(lng);
        y = Math.cos(lat) * Math.sin(lng);
        z = Math.sin(lat);
        X += x;
        Y += y;
        Z += z;
    });

    X = X / total;
    Y = Y / total;
    Z = Z / total;

    var Lng = Math.atan2(Y, X);
    var Hyp = Math.sqrt(X * X + Y * Y);
    var Lat = Math.atan2(Z, Hyp);
    if (isNaN(Lng)|| isNaN(Lat)) {
      return [0, 0]
    }
    return [Lng * 180 / Math.PI, Lat * 180 / Math.PI]
}

const COLOR_MAP = [
  'transparent',
  '#77bfcd',
  '#91d8ed',
  '#009bd7'
]

const fenceBaseOptions = {
  strokeWeight: 1,
  strokeColor: '#fff'
}

export default {
  name: 'app-fence-map',
  data () {
    return  {
      center: '天津',
      height: '100%',
      cityFence: {
        path: city[0]
        // strokeColor: ,
        // strokeOpacity: Number,
        // strokeWeight: Number,
      },
      districtFences: districts.features,
      plates,
      COLOR_MAP,
      fenceBaseOptions,
      markerVisible: false
    }
  },
  methods: {
    getAreaCenter,
    getDistrictTitle (name) {
      return `<span class="district-title">${name}</span>`
    }
  }
}
</script>

<style>
html, body {
  height: 100%;
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}

.map-control {
  position: fixed;
  top: 1rem;
  right: 1rem;
  background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 2px 6px 0 rgba(114, 124, 245, .5);
  padding: 0.75rem;
}

.amap-marker {
  font-size: 14px;
}

.district-title {
  font-size: 20px;
  color: #fff;
}
</style>
