<script>
  import mixin from '../util/mixin'
  export default {
    name: 'amap-mousetool',
    mixins: [mixin],
    data () {
      return {
        events: [
          'draw'
        ]
      }
    },
    methods: {
      load () {
        return new Promise(resolve => {
          global.AMap.plugin(['AMap.MouseTool'], () => {
            this.mousetool = new global.AMap.MouseTool(this.amap)
            this.bindEvent(this.mousetool)
            resolve(this.mousetool)
          })
        })
      },
      marker (opt) {
        this.queue.exec(() => {
          this.mousetool.marker(opt)
        })
      },
      polyline (opt) {
        return this.mousetool.polyline(opt)
      },
      polygon (opt) {
        return this.mousetool.polygon(opt)
      },
      circle (opt) {
        return this.mousetool.circle(opt)
      },
      rule (opt) {
        return this.mousetool.rule(opt)
      },
      measureArea (opt) {
        return this.mousetool.measureArea(opt)
      },
      rectZoomIn (opt) {
        return this.mousetool.rectZoomIn(opt)
      },
      rectZoomOut (opt) {
        return this.mousetool.rectZoomOut(opt)
      },
      close (opt) {
        return this.mousetool.close(opt)
      }
    }
  }
</script>
