import _ from 'lodash'

export function oneOf (value, valueList) {
  for (let i = 0; i < valueList.length; i++) {
    if (value === valueList[i]) {
      return true
    }
  }
  return false
}

export function clearEmptyButBoolean (obj) {
  const query = {}
  _.forEach(obj, (value, key) => {
    if (value || parseInt(value) === 0 || typeof value === 'boolean') {
      query[key] = value
    }
  })
  return query
}

