<template>
<div></div></template>

<script>
  import mixin from '../util/mixin'
  export default {
    name: 'amap-heatmap',
    mixins: [mixin],
    props: {
      opacity: {
        type: Array,
        default () {
          return [0, 0.8]
        }
      },
      radius: {
        type: Number,
        default: 25
      },
      gradient: {
        type: Object
      },
      data: Array,
      max: Number
    },
    watch: {
      data: 'render',
      max: 'render'
    },
    methods: {
      async load () {
        const { AMap } = this
        return new Promise(resolve => {
          AMap.plugin(['AMap.Heatmap'], () => {
            this.heatmap = new AMap.Heatmap(this.amap, {
              radius: this.radius,
              gradient: this.gradient
            })
            resolve(this.heatmap)
          })
        })
      },
      render () {
        if (!this.heatmap) {
          return
        }
        this.heatmap.setDataSet({
          data: this.data,
          max: this.max
        })
      }
    }
  }
</script>
