import queuer from '../../../utils/queuer.js'

export default {
  render () {},
  data () {
    this.overlay = null
    this.editor = null
    return {
      queue: queuer()
    }
  },
  beforeDestroy () {
    if (this.amap && this.overlay) {
      this.amap.remove(this.overlay)
      this.amap = null
    }
  },
  mounted () {
    this.$parent.onLoad(async amap => {
      this.AMap = global.AMap
      this.AMapUI = global.AMapUI
      this.amap = amap
      const obj = await this.load()
      this.queue.ready()
      this.$emit('ready', this, obj)
    })
  },
  methods: {
    bindEvent (obj, events = []) {
      for (const name of events) {
        obj.on(name, e => {
          this.$emit(name, e, obj)
        })
      }
    },
    off (name, fn) {
      this.overlay && this.overlay.off(name, fn)
    },
    onLoad (fn) {
      this.queue.exec(() => {
        fn(this.amap)
      })
    }
  }
}
