<template>
<div></div></template>
<script>
  import mixin from '../util/mixin'
  export default {
    name: 'amap-contextmenu-item',
    mixins: [mixin],
    props: {
      text: String,
      num: Number
    },
    methods: {
      load () {
        console.log(111)
        this.$parent.contextmenu.addItem(this.name, () => {
          this.cb()
        }, this.num)
      },
      cb () {
        this.$emit('click')
      }
    }
  }
</script>
