<template>
<div></div>
</template>
<script>
  import mixin from '../util/mixin'
  import { clearEmptyButBoolean } from '../../../utils/snippet.js'
  export default {
    name: 'amap-scale',
    mixins: [mixin],
    props: {
      offset: {
        type: Array
      },
      position: String,
      bottom: String
    },
    watch: {
      bottom (val) {
        this.obj.a.H.style.bottom = val
      }
    },
    methods: {
      async load () {
        const { AMap } = this
        let offset
        if (this.offset && this.offset.length === 2) {
          offset = new AMap.Pixel(this.offset[0], this.offset[1])
        }
        this.obj = new AMap.Scale(clearEmptyButBoolean({
          position: this.position,
          offset
        }))
        this.amap.addControl(this.obj)
      },
      show () {
        this.obj.show()
      },
      hide () {
        this.obj.hide()
      }
    }
  }
</script>
