import AMap from './Map'
import AMapCircle from './overlay/Circle'
import AMapMarker from './overlay/Marker'
import AMapPolygon from './overlay/Polygon'
import AMapPolyline from './overlay/Polyline'
import AMapMouseTool from './plugin/MouseTool'
import AMapPathSimplifier from './plugin/PathSimplifier'
import AMapHeatmap from './plugin/Heatmap'
import AMapContextMenu from './contextmenu/ContextMenu'
import AMapContextMenuItem from './contextmenu/Item'
import AMapScale from './widget/Scale'
import AMapToolbar from './widget/Toolbar.vue'
import AMapRangingTool from './widget/RangingTool.vue'

export default {
  install (Vue, options) {
    Vue.component('amap', AMap)
    Vue.component('amap-circle', AMapCircle)
    Vue.component('amap-marker', AMapMarker)
    Vue.component('amap-polygon', AMapPolygon)
    Vue.component('amap-polyline', AMapPolyline)
    Vue.component('amap-mousetool', AMapMouseTool)
    Vue.component('amap-contextmenu', AMapContextMenu)
    Vue.component('amap-contextmenu-item', AMapContextMenuItem)
    Vue.component('amap-path-simplifier', AMapPathSimplifier)
    Vue.component('amap-heatmap', AMapHeatmap)
    Vue.component('amap-scale', AMapScale)
    Vue.component('amap-toolbar', AMapToolbar)
    Vue.component('amap-rangingtool', AMapRangingTool)
  }
}
