<template>
<div></div>
</template>

<style lang="scss">
  .amap-toolbar {
    bottom: 60px;
  }
</style>


<script>
  import mixin from '../util/mixin'
  export default {
    name: 'amap-toolbar',
    mixins: [mixin],
    props: {
      offset: {
        type: Array
      },
      position: String,
      bottom: String
    },
    watch: {
      bottom (val) {
        this.obj.a.H.style.bottom = val
      }
    },
    methods: {
      async load () {
        const { AMap } = this
        let offset
        if (this.offset && this.offset.length === 2) {
          offset = new AMap.Pixel(this.offset[0], this.offset[1])
        }
        this.obj = new AMap.ToolBar({
          position: this.position,
          offset,
          direction: false
        })
        this.amap.addControl(this.obj)
      },
      show () {
        this.obj.show()
      },
      hide () {
        this.obj.hide()
      }
    }
  }
</script>
