<script>
  import { clearEmptyButBoolean } from '@/utils/snippet.js'
  import mixin from '../util/mixin'
  import { MARKER_EVENTS } from '../events'
  export default {
    name: 'amap-marker',
    mixins: [mixin],
    props: {
      position: {
        type: Array,
        required: true
      },
      draggable: Boolean,
      offset: Array, // [x, y]
      icon: [String, Object],
      content: [String, Object],
      topWhenClick: Boolean,
      bubble: Boolean,
      raiseOnDrag: Boolean,
      cursor: String,
      visible: Boolean,
      zIndex: Number,
      angle: Number,
      autoRotation: Boolean,
      animation: String,
      shadow: Object,
      title: String,
      clickable: Boolean,
      shape: Object,
      extData: null,
      label: Object
    },
    watch: {
      label: 'setLabel',
      offset (v) {
        this.method('setOffset', v)
        this.setOffset()
      },
      position (v) {
        this.method('setPosition', v)
      },
      clickable (v) {
        this.method('setClickable', v)
      },
      angle (v) {
        this.method('setAngle', v)
      },
      zIndex (v) {
        this.method('setzIndex', v)
      },
      icon: 'setIcon',
      draggable (v) {
        this.method('setDraggable', v)
      },
      content (v) {
        this.method('setContent', v)
      },
      title (v) {
        this.method('setTitle', v)
      },
      shadow (v) {
        this.method('setShadow', v)
      },
      animation (v) {
        this.method('setAnimation', v)
      },
      visible (v) {
        if (v) {
          this.method('show')
        } else {
          this.method('hide')
        }
      }
    },
    methods: {
      async load () {
        const { AMap } = this
        // console.log(this.label)
        this.overlay = new AMap.Marker(clearEmptyButBoolean({
          map: this.amap,
          draggable: this.draggable,
          position: this.position,
          offset: this.offset,
          content: this.content,
          topWhenClick: this.topWhenClick,
          bubble: this.bubble,
          raiseOnDrag: this.raiseOnDrag,
          cursor: this.cursor,
          visible: this.visible,
          zIndex: this.zIndex,
          angle: this.angle,
          autoRotation: this.autoRotation,
          animation: this.animation,
          shadow: this.shadow,
          title: this.title,
          clickable: this.clickable,
          shape: this.shape,
          extDat: this.extDat
        }))
        this.setIcon()
        this.setLabel()
        this.setOffset()
        this.bindEvent(this.overlay, MARKER_EVENTS)
        return this.overlay
      },
      setLabel () {
        if (this.label) {
          const { AMap } = this
          const { offset = [0, 0], content } = this.label
          this.overlay.setLabel({
            offset: new AMap.Pixel(offset[0], offset[1]),
            content
          })
        }
      },
      setOffset () {
        if (typeof this.offset !== 'undefined') {
          if (this.offset.length > 0) {
            const { AMap } = this
            const offset = this.offset
            this.overlay.setOffset(new AMap.Pixel(offset[0], offset[1]))
          }
        }
      },
      setIcon () {
        let icon = this.icon
        const { AMap } = this
        if (icon) {
          if (typeof icon === 'object') {
            const { size, image, imageOffset, imageSize } = icon
            icon = {}
            if (size) {
              icon.size = new AMap.Size(size[0], size[1])
            }
            if (image) {
              icon.image = image
            }
            if (imageOffset) {
              icon.imageOffset = new AMap.Pixel(imageOffset[0], imageOffset[1])
            }
            if (imageSize) {
              icon.imageSize = new AMap.Size(imageSize[0], imageSize[1])
            }
          }
          this.overlay.setIcon(new AMap.Icon(icon))
        }
      },
      method (name, params) {
        if (!this.overlay) {
          return
        }
        this.overlay[name](params)
      }
    }
  }

</script>
