<script>
  import mixin from '../util/mixin'
  import { clearEmptyButBoolean } from '../../../utils/snippet.js'
  import { POLYGON_EVENTS } from '../events'
  export default {
    name: 'amap-polygon',
    mixins: [mixin],
    props: {
      path: {
        type: Array,
        required: true
      },
      extData: null,
      zIndex: Number,
      bubble: Boolean,
      strokeColor: String,
      strokeOpacity: Number,
      strokeWeight: Number,
      fillOpacity: Number,
      fillColor: String,
      strokeStyle: String,
      strokeDasharray: Array
    },
    watch: {
      path (v) {
        this.overlay.setPath(v)
      },
      extData (v) {
        this.overlay.setExtData(v)
      },
      zIndex: 'setOptions',
      bubble: 'setOptions',
      strokeColor: 'setOptions',
      strokeOpacity: 'setOptions',
      strokeWeight: 'setOptions',
      fillOpacity: 'setOptions',
      fillColor: 'setOptions',
      strokeStyle: 'setOptions',
      strokeDasharray: 'setOptions'
    },
    methods: {
      load () {
        const AMap = global.AMap
        return new Promise(resolve => {
          this.overlay = new AMap.Polygon(clearEmptyButBoolean({
            path: this.path,
            extData: this.extData
          }))
          this.bindEvent(this.overlay, POLYGON_EVENTS)
          this.setOptions()
          this.overlay.setMap(this.amap)
          if (this.editable) {
            global.AMap.plugin(['AMap.PolyEditor'], () => {
              this.editor = new global.AMap.PolyEditor(this.amap, this.overlay)
              resolve(this.overlay)
            })
          } else {
            resolve(this.overlay)
          }
        })
      },
      setOptions () {
        if (!this.overlay) {
          return
        }
        this.overlay.setOptions(clearEmptyButBoolean({
          zIndex: this.zIndex,
          bubble: this.bubble,
          strokeColor: this.strokeColor,
          strokeOpacity: this.strokeOpacity,
          strokeWeight: this.strokeWeight,
          fillOpacity: this.fillOpacity,
          fillColor: this.fillColor,
          strokeStyle: this.strokeStyle,
          strokeDasharray: this.strokeDasharray
        }))
      },
      contains (point) {
        return this.overlay.contains(point)
      },
      openEditor () {
        this.editor && this.editor.open()
      },
      closeEditor () {
        this.editor && this.editor.close()
      },
      fitView () {
        this.queue.exec(() => {
          this.amap.setFitView(this.overlay)
        })
      }
    }
  }
</script>
