<template>
  <div class="amap-rangingtool pointer" @click="turnOn" :style="{'bottom': bottom}">
    <i class="icon icon-ranging tool-color" :class="{'active': active}"></i>
  </div>
</template>
<script>
  import mixin from '../util/mixin'
  export default {
    name: 'amap-rangingtool',
    mixins: [mixin],
    props: {
      bottom: String
    },
    data () {
      return {
        active: false,
        toggle: true
      }
    },
    methods: {
      async load () {
        const { AMap } = this
        this.obj = new AMap.RangingTool(this.amap)
        this.amap.addControl(this.obj)
      },
      turnOn () {
        if (this.toggle && this.active) {
          this.turnOff()
          return
        }
        this.active = true
        this.obj.turnOn()
      },
      turnOff () {
        this.active = false
        this.obj.turnOff()
      }
    }
  }
</script>
<style lang="scss">
  .amap-rangingtool {
    position: absolute;
    width: 32px;
    height: 32px;
    line-height: 32px;
    text-align: center;
    z-index: 1000;
    bottom: 280px;
    left: 10px;
    background: #fff;
    box-shadow: 1px 2px 7px 0px rgba(0, 0, 0, 0.35);
    &:hover .icon {
      color: #00b38a;
    }
  }
  .icon {
    font-size: 16px;
  }
  .tool-color {
    color: #80848f;
  }
  .active {
    color: #04A37E;
  }
</style>
