<script>
  import { clearEmptyButBoolean } from '../../../utils/snippet.js'
  import mixin from '../util/mixin'
  import { CIRCLE_EVENTS } from '../events'
  export default {
    name: 'amap-circle',
    mixins: [mixin],
    props: {
      center: {
        type: Array,
        required: true
      },
      radius: {
        type: Number,
        required: true
      },
      editable: Boolean,
      zIndex: Number,
      bubble: Boolean,
      strokeColor: String,
      strokeOpacity: Number,
      strokeWeight: Number,
      fillColor: String,
      fillOpacity: Number,
      strokeStyle: String,
      extData: null,
      strokeDasharray: Array,
      visible: {
        type: Boolean,
        default: true
      }
    },
    watch: {
      center: 'setCenter',
      radius: 'setRadius',
      strokeColor: 'setOptions',
      strokeOpacity: 'setOptions',
      strokeWeight: 'setOptions',
      fillColor: 'setOptions',
      fillOpacity: 'setOptions',
      strokeStyle: 'setOptions',
      strokeDasharray: 'setOptions',
      visible (v) {
        if (v) {
          this.show()
        } else {
          this.hide()
        }
      }
    },
    methods: {
      load () {
        const AMap = global.AMap
        return new Promise(resolve => {
          this.overlay = new AMap.Circle({
            center: this.center,
            radius: this.radius
          })
          this.bindEvent(this.overlay, CIRCLE_EVENTS)
          this.setOptions()
          this.overlay.setMap(this.amap)
          if (this.editable) {
            global.AMap.plugin(['AMap.CircleEditor'], () => {
              this.editor = new global.AMap.CircleEditor(this.amap, this.overlay)
              resolve(this.overlay)
            })
          } else {
            resolve(this.overlay)
          }
        })
      },
      setOptions () {
        if (!this.overlay) {
          return
        }
        this.overlay.setOptions(clearEmptyButBoolean({
          zIndex: this.zIndex,
          bubble: this.bubble,
          strokeColor: this.strokeColor,
          strokeOpacity: this.strokeOpacity,
          fillColor: this.fillColor,
          fillOpacity: this.fillOpacity,
          strokeWeight: this.strokeWeight,
          strokeStyle: this.strokeStyle,
          extData: this.extData,
          strokeDasharray: this.strokeDasharray
        }))
      },
      setCenter () {
        return this.overlay.setCenter(this.center)
      },
      setRadius () {
        return this.overlay.setRadius(this.radius)
      },
      show () {
        this.overlay.show()
      },
      hide () {
        this.overlay.hide()
      },
      openEditor () {
        this.editor && this.editor.open()
      },
      closeEditor () {
        this.editor && this.editor.close()
      }
    }
  }
</script>
