<template>
<div></div></template>
<script>
  import mixin from '../util/mixin'
  export default {
    name: 'amap-contextmenu',
    mixins: [mixin],
    methods: {
      async load () {
        console.log(1111)
        this.contextmenu = new global.AMap.ContextMenu()
        if (this.$parent.overlay) {
          this.$parent.overlay.on('rightclick', e => {
            this.contextmenu.open(this.amap, e.lnglat)
          })
        }
      }
    }
  }
</script>
